<template>
    <FileBrowser 
        v-if="VideoBrowserOpen" 
        :isWindowsOpen="true" 
        :callbackFunction="'video'"
        :allowFileType="['mp4','m3u8']" 
        @file-browser-trigger="changeVideoTrigger" 
        @file-browser-callback="selectVideoTrigger"
    />

    <FileBrowser 
        v-if="DocumentBrowserOpen" 
        :isWindowsOpen="true" 
        :callbackFunction="'document'"
        :allowFileType="['pdf','doc','docx','xls','xlsx','csv']" 
        @file-browser-trigger="changeDocumentTrigger" 
        @file-browser-callback="selectDocumentTrigger"
    />

    <SetContentSource 
    v-if="SetContentSourceModal" 
    :isWindowsOpen="true" 
    :setContentTriggerObj="getContentTriggerObj"
    :contentType="initialSelectPlayerItemType" 
    :contentOriginal="getContentTriggerObj" 
    @set-content-trigger="SetContentTrigger" 
    @set-content-event="handleSetContentSource"
/>

    <div class="bg-white" @click.stop="handleCheckboxClick">
        <!-- Header Section -->
        <div class="border-b border-gray-200">
            <div class="grid grid-cols-3">
                <div class="col-span-1 p-2">
                    <div class="flex justify-left items-left">
                        <div class="relative inline-block text-left">

                            <select 
                                v-model="type" 
                                id="content-type" 
                                class="mt-1 block w-full p-2 rounded border border-gray-300 focus:ring focus:ring-indigo-200" 
                                :disabled="isTypeChangeDisabled" 
                                @click.stop
                                @change="handleTypeChange">
                                <option value="" disabled></option>
                                <template v-for="(typeItem) in playerType" :key="typeItem.code">
                                    <option :value="typeItem.code">{{ typeItem.name }}</option>
                                </template>
                            </select>
                        
                        </div>
                    </div>
                </div>
                <div class="col-span-1 p-2 flex justify-center items-center">
                    <p class="text-center">{{ selectPlayerItem.duration }} </p>
                </div>
                <div class="col-span-1 p-2 flex justify-end items-center">
                    <button 
                        @click.stop="cancelUpdate"
                        type="button" 
                        class="mr-1 border border-transparent bg-white shadow-sm border border-gray-400 px-4 py-2 text-sm font-medium text-gray-600 shadow-sm hover:bg-gray-100">
                        <font-awesome-icon :icon="['fas','times']" class="text-gray-600 pr-2"/>  
                    </button>
                </div>
            </div>
        </div>

        

        <div v-if="['demand', 'streaming', 'youtube', 'live'].includes(selectPlayerItem.type) || ['demand', 'streaming', 'youtube', 'live'].includes(type)">
            <div class="relative">
                <template v-if="selectPlayerItem.video">
                    <video controls class="w-full h-60" v-if="type === 'demand' || initialSelectPlayerItemType === 'demand'">
                        <source :src="selectPlayerItem.video.desktop" type="video/mp4">
                        Your browser does not support the video tag.
                    </video>

                    <div style="position:relative;padding-top:56.25%;" v-if="type === 'streaming' || initialSelectPlayerItemType === 'streaming'">
                        <iframe :src="getIframeSrc(selectPlayerItem.video.streaming)" loading="lazy" 
                                style="border:0;position:absolute;top:0;height:100%;width:100%;" 
                                allow="accelerometer;gyroscope;autoplay;encrypted-media;picture-in-picture;" 
                                allowfullscreen="true">
                        </iframe>
                    </div>

                    <video controls class="w-full h-60" v-if="type === 'live' || initialSelectPlayerItemType === 'live'">
                        <source :src="selectPlayerItem.video.live" type="application/x-mpegURL">
                        Your browser does not support the video tag.
                    </video>
                    
                    <iframe width="100%" height="300" v-if="type === 'youtube' || initialSelectPlayerItemType === 'youtube'" :src="selectPlayerItem.video.youtube" frameborder="0" allowfullscreen></iframe>
                </template>
                  
                <template v-else>
                    <div class="bg-gray-100 min-h-[200px] h-full flex justify-center items-center rounded text-xs">
                        <span class="bg-black text-white p-2">!! </span>
                    </div>
                </template>

                <div class="absolute top-10 left-1/2 transform -translate-x-1/2 -translate-y-1/2">
                    <button 
                    @click.stop="handleButtonClick(buttonType)" 
                    class="bg-yellow-500 hover:bg-yellow-600 text-white text-md px-2 py-1 rounded">
                    <font-awesome-icon :icon="['fas', 'folder-open']" class="pt-2 text-white text-md" />
                    <br>
                    <span class="pt-2 text-sm">
                        {{ buttonType === 'live' ? ' Live' : (buttonType !== 'youtube' && buttonType !== 'streaming' ? '' : '') }}
                    </span>
                    </button>
                
                    
                    <button @click.stop="removeVideo" class="ml-2 bg-red-500 hover:bg-red-600 text-white text-md px-2 py-1 rounded">
                        <font-awesome-icon :icon="['fas','trash']" class="pt-2 text-white text-md"/><br>
                        <span class="pt-2 text-sm"></span>
                    </button>
                </div>
            </div>

            <!-- Footer Section (Grid Like Header) -->
            <div class="border-t border-gray-200 pb-1">
                <div class="grid grid-cols-1">
                    <div class="col-span-1 p-2">
                        <span class="font-medium text-gray-500">
                            <font-awesome-icon :icon="['fas','video']" class="text-rose-600" :class="selectPlayerItem.video===undefined ? 'text-rose-600' : 'text-lime-600'"/>
                            <span v-if="(selectPlayerItem.video===undefined)" class="ml-2 bg-blue-100 pl-2 pr-2 rounded text-xs"></span>
                            <span v-if="(selectPlayerItem.video!=undefined && selectPlayerItem.type==='demand')" class="ml-2 bg-blue-100 pl-2 pr-2 rounded text-xs">{{ selectPlayerItem.video.desktop }}</span>
                            <span v-if="(selectPlayerItem.video!=undefined && selectPlayerItem.type==='streaming')" class="ml-2 bg-blue-100 pl-2 pr-2 rounded text-xs">{{ selectPlayerItem.video.streaming }}</span>
                            <span v-if="(selectPlayerItem.video!=undefined && selectPlayerItem.type==='youtube')" class="ml-2 bg-blue-100 pl-2 pr-2 rounded text-xs">{{ selectPlayerItem.video.youtube }}</span>
                            <span v-if="(selectPlayerItem.video!=undefined && selectPlayerItem.type==='live')" class="ml-2 bg-blue-100 pl-2 pr-2 rounded text-xs">{{ selectPlayerItem.video.live }}</span>
                        </span>
                    </div>
                </div>
            </div>
        </div>

        <div v-if="['document', 'exam'].includes(selectPlayerItem.type) || ['document', 'exam'].includes(type)">
            <template v-if="selectPlayerItem.content">
                <div 
                    class="bg-gray-100 min-h-[100px] h-full flex justify-center items-center rounded text-xs" 
                    v-if="type === 'document' || initialSelectPlayerItemType === 'document'">
                    {{ selectPlayerItem.content }}
                </div>
                <div 
                    class="bg-gray-100 min-h-[100px] h-full flex justify-center items-center rounded text-xs" 
                    v-if="type === 'exam' || initialSelectPlayerItemType === 'exam'">
                </div>
            </template>
              
            <template v-else>
                <div class="bg-gray-100 min-h-[100px] h-full flex justify-center items-center rounded text-xs"></div>
            </template>

            <div class="absolute top-28 left-1/2 transform -translate-x-1/2 -translate-y-1/2">
                <button @click.stop="handleButtonClick(buttonType)" class="bg-yellow-500 hover:bg-yellow-600 text-white text-md px-2 py-1 rounded">
                    <font-awesome-icon :icon="['fas','folder-open']" class="pt-2 text-white text-md"/><br>
                    <span class="pt-2 text-sm">{{ buttonType === 'document' ? '' : (buttonType === 'exam' ? '' : '') }}</span>
                </button>
                <button @click.stop="removeVideo" class="ml-2 bg-red-500 hover:bg-red-600 text-white text-md px-2 py-1 rounded">
                    <font-awesome-icon :icon="['fas','trash']" class="pt-2 text-white text-md"/><br>
                    <span class="pt-2 text-sm"></span>
                </button>
            </div>
        </div>

        <div v-if="selectPlayerItem.type === 'exam'" class="p-2">
            <div class="bg-gray-100 p-3 rounded shadow-md">
                <h3 class="font-medium text-lg mb-1"></h3>
                <input v-model="newQuestion" placeholder="" class="w-full p-1 border border-gray-300 rounded mb-1" />
                <button @click.stop="addQuestion" class="w-full bg-blue-500 text-white p-1 rounded hover:bg-blue-600"></button>
            </div>
            
            <div class="mt-2">
                <label>
                    <input type="checkbox" v-model="examOptionValid" />
                    Exam Option
                </label>
            </div>

            <div v-if="examOptionValid" class="col-span-1">
                <label for="validScore" class="block text-base font-medium text-gray-900"></label>
                <input
                    v-model="validScore"
                    @click.stop="handleCheckboxClick"
                    type="number"
                    autocomplete="validScore"
                    class="mt-1 block w-full rounded-sm border border-gray-300 py-2 px-3 shadow-sm focus:border-gray-900 focus:outline-none focus:ring-gray-900 sm:text-sm"
                />
            </div>
            
            <div v-if="questions.length" class="mt-2">
                <h4 class="font-medium text-lg">:</h4>
                <ul class="list-disc pl-5">
                    <li v-for="(question, index) in questions" :key="question.id" class="mb-2">
                        <div class="font-semibold">{{ question.text }}</div>
                        <div class="mt-1">
                            <input v-model="question.newAnswer" placeholder="" class="w-full p-1 border border-gray-300 rounded mb-1" />
                            <button @click.stop="addAnswerToQuestion(index)" class="bg-green-500 text-white p-1 rounded hover:bg-green-600"></button>
                            <button @click.stop="removeQuestion(index)" class="ml-2 bg-red-500 text-white p-1 rounded hover:bg-red-600"></button>
                        </div>
                        <ul class="mt-1">
                            <li v-for="(answer, answerIndex) in question.answers" :key="answer.id" class="flex items-center mb-1">
                                <input type="radio" :value="answer.id" v-model="question.correctAnswer" class="mr-1" />
                                <span>{{ answer.text }}</span>
                                <button @click.stop="removeAnswer(index, answerIndex)" class="ml-2 bg-red-500 text-white p-1 rounded hover:bg-red-600"></button>
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
            <div class="mt-4">
                <button @click.stop="updateExam" class="bg-indigo-600 text-white p-2 rounded hover:bg-indigo-700">
                    
                </button>
            </div>
        </div>

        <div class="border-t border-gray-200 grid grid-cols-2 gap-4 px-5 py-3">
            <div class="col-span-1">
                <label for="name" class="block text-base font-medium text-gray-900"></label>
                <input
                    v-model="name"
                    @click.stop="handleCheckboxClick"
                    type="text"
                    autocomplete="name"
                    class="mt-1 block w-full rounded-sm border border-gray-300 py-2 px-3 shadow-sm focus:border-gray-900 focus:outline-none focus:ring-gray-900 sm:text-sm"
                />
            </div>
  
            <div class="col-span-1">
                <label for="duration" class="block text-base font-medium text-gray-900"> ()</label>
                <input
                    v-model="duration"
                    @click.stop="handleCheckboxClick"
                    type="text"
                    autocomplete="duration"
                    class="mt-1 block w-full rounded-sm border border-gray-300 py-2 px-3 shadow-sm focus:border-gray-900 focus:outline-none focus:ring-gray-900 sm:text-sm"
                />
            </div>

            <div class="col-span-1">
                <label class="block text-base font-medium text-gray-900">
                    <input
                        v-model="demo"
                        @click.stop="handleCheckboxClick"
                        type="checkbox"
                        class="mt-1 rounded-sm border border-gray-300 shadow-sm focus:border-gray-900 focus:outline-none focus:ring-gray-900 sm:text-sm"
                    />
                    
                </label>
            </div>
    
            <div class="col-span-2">
                <label for="slug" class="block text-base font-medium text-gray-900"></label>
                <textarea 
                    v-model="description" 
                    @click.stop="handleCheckboxClick" 
                    rows="3" 
                    cols="20" 
                    class="mt-1 block w-full rounded-sm border border-gray-300 py-2 px-3 shadow-sm focus:border-gray-900 focus:outline-none focus:ring-gray-900 sm:text-sm">
                </textarea>
            </div>

            
            
        </div>
  
        <div class="bg-gray-50 border-t border-gray-200 p-2 text-right flex justify-between">
            <div class="flex items-center space-x-4">
                <button 
                    @click.stop="deleteItem"
                    type="button" 
                    class="border border-transparent bg-red-600 px-4 py-2 text-sm font-medium text-white shadow-sm hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-offset-2">
                    <font-awesome-icon :icon="['fas','trash']" class="text-gray-200 pr-2"/> 
                </button>

                <SpecialAssign 
                :player="selectPlayerItem"
                @set-content-reload="handleDataSource"
                />
    
            </div>
    
            <div>
                <button 
                    @click.stop="saveChanges"
                    type="button" 
                    class="border border-transparent bg-indigo-600 px-4 py-2 text-sm font-medium text-white shadow-sm hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2">
                    <font-awesome-icon :icon="['fas','save']" class="text-gray-200 pr-2"/> 
                </button>
            </div>
        </div>
    </div>
</template>

<script>
import SetContentSource from '@/interface/modal/SetContentSource.vue';
import FileBrowser from '@/interface/modal/FileBrowser.vue';
import SpecialAssign from '@/extensions/modules/elearning/lesson/component/course/resource/SpecialAssign.vue';
import { player } from "@/master/type";

export default {
    props: {
        selectPlayerItem: {
            type: Object,
            required: true,
        },
    },
    data() {
        return {
            playerType: player,
            id: this.selectPlayerItem._id,
            name: this.selectPlayerItem.name,
            duration: this.selectPlayerItem.duration,
            description: this.selectPlayerItem.description,
            type: this.selectPlayerItem.type,
            demo: this.selectPlayerItem.demo,
            not_visible: this.selectPlayerItem.not_visible,
            status: this.selectPlayerItem.status,

            VideoBrowserOpen: false,
            SetContentSourceModal: false,
            DocumentBrowserOpen: false,

            initialSelectPlayerItemType: this.selectPlayerItem.type,
            buttonType: this.selectPlayerItem.type,

            newQuestion: '',
            questions: this.selectPlayerItem.content ? this.selectPlayerItem.content.exam || [] : [],
            examOptionValid: false,
            validScore: '',
        };
    },
    components: {
        FileBrowser,
        SetContentSource,
        SpecialAssign
    },
    computed: {
        getContentTriggerObj() {
            if (!this.selectPlayerItem.video) {
                return null;
            }
            if (this.selectPlayerItem.type === 'streaming') {
                return this.selectPlayerItem.video.streaming;
            } else if (this.selectPlayerItem.type === 'youtube') {
                return this.selectPlayerItem.video.youtube;
            } else if (this.selectPlayerItem.type === 'live') {
                return this.selectPlayerItem.video.live;
            } else {
                return null;
            }
        },
        isTypeChangeDisabled() {
            //  folder  sub items 
            return this.selectPlayerItem.type === 'folder' && 
                   this.selectPlayerItem.items && 
                   this.selectPlayerItem.items.length > 0;
        },
        formattedQuestions() {
            return {
                exam: this.questions.map(question => ({
                    id: question.id,
                    text: question.text,
                    answers: question.answers,
                    newAnswer: question.newAnswer,
                    correctAnswer: question.correctAnswer,
                })),
                option: {
                    valid: this.examOptionValid, //  Exam Option
                    score: this.validScore, //  Exam Option
                },
            };
        },
    },
    watch: {
        type(newType) {
            this.initialSelectPlayerItemType = newType;
            this.buttonType = newType;
        },
    },
    methods: {
        extractVideoID(url) {
            //  url  undefined  null 
            if (!url) return null;
            
            const match = url.match(/\/([a-f0-9-]+)\//);
            return match ? match[1] : null;
        },
        getIframeSrc(videoUrl) {
            //  videoUrl  undefined  null 
            if (!videoUrl) return '';
            
            const videoID = this.extractVideoID(videoUrl);
            if (videoID) {
                return `https://iframe.mediadelivery.net/embed/37374/${videoID}?autoplay=false&loop=false&muted=false&preload=true&responsive=true`;
            }
            return '';
        },
        handleCheckboxClick(event) {
            event.stopPropagation();
        },
        saveChanges() {
            const updatedItem = {
                id: this.id,
                name: this.name,
                duration: this.duration,
                description: this.description,
                type: this.type,
                demo: this.demo,
                not_visible: this.not_visible,
                status: this.status,
            };
            this.$emit('update-select-player-item', updatedItem);
        },
        deleteItem() {
            const updatedItem = {
                id: this.id,
            };
            this.$emit('delete-select-player-item', updatedItem);
        },
        removeVideo() {
            const updatedItem = {
                id: this.id,
            };
            this.$emit('remove-select-player-item', updatedItem);
        },
        cancelUpdate() {
            this.$emit('cancel-update');
        },
        handleButtonClick(type) {
            console.log("type",type);
            if (type === 'demand') {
                this.OpenVideoBrowser();
            } else if (type === 'document') {
                this.OpenDocumentBrowser();
            } else if (type === 'exam') {
                this.OpenExamBrowser();
            } else {
                this.openSetContentSource(this.selectPlayerItem, type);
            }
        },
        OpenDocumentBrowser() {
            this.DocumentBrowserOpen = true;
        },
        changeDocumentTrigger(payload) {
            this.DocumentBrowserOpen = payload;
        },
        selectDocumentTrigger(payload) {
            if (payload != undefined) {
                const updatedItem = {
                    id: this.id,
                    file: payload.file,
                };
                this.$emit('update-player-document', updatedItem);
            }
        },
        OpenVideoBrowser() {
            this.VideoBrowserOpen = true;
        },
        changeVideoTrigger(payload) {
            this.VideoBrowserOpen = payload;
        },
        selectVideoTrigger(payload) {
            if (payload != undefined) {
                const updatedItem = {
                    id: this.id,
                    file: payload.file,
                    duration: payload.duration,
                    thumbnail: payload.thumbnail,
                };
                this.$emit('update-player-video', updatedItem);
            }
        },
        updateExam(payload) {
            if (payload != undefined) {
                const updatedItem = {
                    id: this.id,
                    questions: this.formattedQuestions,
                };
                this.$emit('update-player-exam', updatedItem);
            }
        },
        openSetContentSource(item, type) {
            console.log("type",item,type);
            this.setContentTriggerObj = item;
            this.contentType = type;
            this.SetContentSourceModal = true;
        },
        SetContentTrigger(payload) {
            console.log("SetContentTrigger",payload);
            this.SetContentSourceModal = payload;
        },
        handleSetContentSource(payload) {
            if (payload != undefined) {
                const updatedItem = {
                    id: this.id,
                    type: this.type,
                    file: payload.contentURL,
                };
                this.$emit('update-player-video-url', updatedItem);
                this.SetContentSourceModal = false;  // Close modal after setting content source
            }
        },
        handleDataSource() {
            console.log("handleDataSource");
            this.$emit('update-player-data');
        },
        handleTypeChange() {
            this.saveChanges();
        },
        OpenExamBrowser() {
            // Implementation for opening exam browser
        },
        addQuestion() {
            if (this.newQuestion) {
                const newQuestion = {
                    id: Date.now(), //  ID  timestamp
                    text: this.newQuestion,
                    answers: [],
                    newAnswer: '', // 
                    correctAnswer: '', //  ( ID)
                };

                //  questions
                this.questions.push(newQuestion);
                
                //  newQuestion
                this.newQuestion = '';
            }
        },
        addAnswerToQuestion(index) {
            const question = this.questions[index];
            if (question.newAnswer) {
                question.answers.push({
                    id: Date.now(), //  ID  timestamp
                    text: question.newAnswer,
                });
                question.newAnswer = ''; // 
            }
        },
        removeQuestion(index) {
            this.questions.splice(index, 1);
        },
        removeAnswer(questionIndex, answerIndex) {
            this.questions[questionIndex].answers.splice(answerIndex, 1);
        },
    },
};
</script>

<style scoped>
/*  */
.bg-gray-100 {
    background-color: #f7fafc;
}
</style>
